<template>
    <div>
        <div class="tabs">
            <ul id='tabs-ui'>
                <li
                    v-for="(tab, index) in tabs"
                    :key="index"
                    :class="[ tab.isActive?'active':'', `${tab.classCustom}` ]">
                    <div
                        class="tab-name"
                        @click="selectTab(tab, index)">{{ tab.name }}</div>
                </li>
            </ul>
        </div>
        <div class="tabs-details">
            <slot />
        </div>
    </div>
</template>
<script>
export default {
    name: 'Tabs',
    data() {
        return {
            tabs: [],
            selectedTabIndex: 0,
        };
    },
    created() {
        this.tabs = this.$children;
    },
    methods: {
        selectTab(selectedTab, index) {
            this.tabs.forEach((tab) => {
                tab.isActive = (tab.name === selectedTab.name); // eslint-disable-line
            });

            this.$emit('select', { selectedTab, index });
        },
    },
};
</script>
