<template>
    <gray-mask :show="show">
        <div class="dialog">
            <div class="light" />
            <div
                class="close"
                @click="closeDialog"
            />
            <h2 class="title">悄悄告诉你  赚金币秘籍</h2>
            <div class="btns">
                <button
                    class="btn1"
                    @click="goOtherAct('抽免单')">
                    去抽免单
                </button>
                <button
                    class="btn2"
                    @click="goOtherAct('领金币')">
                    去领200金币
                </button>
            </div>
            <p
                class="back"
                @click="goBack">
                继续返回
            </p>
        </div>
    </gray-mask>
</template>

<script>
import GrayMask from '@/components/GrayMask';
import { isXiangkan } from '@/utils/ua';
import { quitCurrentWebview } from '@/utils/jsApiV2/client';

export default {
    components: {
        GrayMask,
    },
    props: {
        page: {
            type: String,
            default: 'PDD商品列表页',
        },
        url: {
            type: String,
            default: 'bikan://web.goto/commonWebView?url_encode=https%3A%2F%2Ffeed.browser.miui.com%2Fmobile-v2%2Fact%2FpddExchange',
        },
    },
    data() {
        return {
            show: true,
        };
    },
    created() {
        this.$o2o({
            e_c: '挽留弹窗',
            e_a: '曝光',
            e_n: this.page,
        });
    },
    methods: {
        closeDialog() {
            this.$o2o({
                e_c: '挽留弹窗关闭',
                e_a: '点击',
                e_n: this.page,
            });
            this.show = false;
            this.$emit('close');
        },
        goBack() {
            this.$o2o({
                e_c: '挽留弹窗继续返回',
                e_a: '点击',
                e_n: this.page,
            });
            if (isXiangkan()) {
                quitCurrentWebview();
            } else {
                this.$router.go(-1);
            }
        },
        goOtherAct(action) {
            this.$o2o({
                e_c: `挽留弹窗${action}`,
                e_a: '点击',
                e_n: this.page,
            });
            location.href = this.url;
        },
    },
};
</script>

