<template>
    <pdd-layout
        page="channel"
        :bottom-text="bottomText">
        <div class="channel">
            <div class="channel-generalize-banner"/>
            <div class="channel-generalize">
                <div
                    v-for="(item,index) in generalizeInfo"
                    :key='index'
                    @click="goGeneralize(item)"
                    class="generalize-goods">
                    <div
                        :style="{
                            backgroundImage: `url(${item.imgUrl})`
                        }"
                        class="goods-img"/>
                    <div class="generalize-text">{{ item.text }}</div>
                </div>
            </div>
            <div class="channel-title">
                今日特价福利还剩
                <span class="channel-title-countdown">
                    <p> {{ countDown.hour }}</p>
                    : <p> {{ countDown.minute }}</p> :
                    <p> {{ countDown.second }}</p>
                </span>
                结束
            </div>
            <pddMallTabs @finish='finish'/>
        </div>
        <BackDialog v-if="dialogShow" />
    </pdd-layout>
</template>

<script>
import { getConfigByKey } from '@/api/common';
import { sget } from '@/utils/cache';
import BackDialog from '@/components/BackDialog';
import pddMallTabs from './components/pddMallTabs';
import PDDLayout from './components/PDDLayout';
import { appllokey, dialogSessionKey, setContext, goBack } from './config';

const bottomText = '- 今日全部优惠都在上面啦 -';
const emptyBottomText = '- 该分类下暂无优惠商品哦 -';
const page = 'PDD商品列表页';

export default {
    name: 'PDDMallChannel',
    components: {
        pddMallTabs,
        pddLayout: PDDLayout,
        BackDialog,
    },
    created() {
        this.$o2o({
            e_c: '首页',
            e_a: '曝光',
            e_n: page,
        });
        this.$root.hidePlaceholder();
        this.getApolloInfo();
        this.getTime();
    },
    data() {
        return {
            timer: null,
            countDown: {
                hour: '12',
                minute: '12',
                second: '12',
            },
            generalizeInfo: [],
            bottomText: '',
            dialogShow: false,
        };
    },
    mounted() {
        const isShowed = sget(dialogSessionKey) !== 'true';
        if (isShowed) {
            setContext(this);
        }
        if (!this.$router.from.name && isShowed) {
            history.replaceState('backDialog', null, document.URL);
            history.pushState(null, null, document.URL);
            window.addEventListener('popstate', goBack);
        }
    },
    methods: {
        getApolloInfo() {
            getConfigByKey({
                key: appllokey,
            }).then((res) => {
                this.generalizeInfo = res.data.pddMall;
            }).catch(() => {
                this.$error();
            });
        },
        goGeneralize(item) {
            if (item['xiangkan-innerActPath']) {
                this.$router.push(item.link);
            } else {
                window.location.href = item.link;
            }
            this.$o2o({
                e_c: '顶部推荐',
                e_a: '点击',
                e_n: page,
                e_x: {
                    name: item.text,
                },
            });
        },
        getTime() {
            clearTimeout(this.timer);
            if (this.computeCountDown() > 0) {
                this.timer = setTimeout(() => {
                    this.getTime();
                }, 1000);
            }
        },
        computeCountDown() {
            const startTime = Date.now();
            // eslint-disable-next-line no-mixed-operators
            const endTime = new Date().setHours(24, 0, 0, 0);
            // eslint-disable-next-line no-mixed-operators
            const diffTime = new Date((endTime - startTime) - 8 * 60 * 60 * 1000);
            const hour = this.checktime(diffTime.getHours());
            const minute = this.checktime(diffTime.getMinutes());
            const second = this.checktime(diffTime.getSeconds());
            this.countDown = {
                hour,
                minute,
                second,
            };
            return endTime - startTime > 0;
        },
        checktime(time) {
            return time < 10 ? `0${time}` : time;
        },
        goShops() {
            this.$router.push('/act/pddMall/shops');
        },
        finish(flag) {
            this.bottomText = flag ? bottomText : emptyBottomText;
        },
    },
};
</script>

