<template>
    <tabs
        @select="selectTab"
        class="channel-tabs">
        <tab
            v-for="(item,index) in tabs"
            :key='index'
            :selected="index===0"
            class-custom="channel-tab"
            :name="item.optName">
            <van-list
                v-model="loading"
                :finished="finished"
                :immediate-check='false'
                @load="getSearchList"
            >
                <div class="channel-goods-list">
                    <div
                        v-for="(item,index) in goodlist"
                        :key='index'
                        @click="goGoodsDetail(item.goodsId)"
                        class="channel-goods" >
                        <img
                            :key='item.goodsId'
                            v-lazy='item.imgUrl'
                            class="channel-goods-img">
                        <div class="channel-container">
                            <div class="channel-goods-name">
                                {{ item.name }}
                            </div>
                            <div>
                                <span class="salecount">
                                    已售{{ item.salesTip }}件
                                </span>
                                <div class="channel-goods-discounts">
                                    <div class="discounts-icon">
                                        省
                                    </div>
                                    {{ item.discount }}元劵
                                </div>
                            </div>
                            <div class="channel-goods-info">
                                <div class="sale">
                                    ￥<span class="sale-symbol">{{ item.computePrice }}</span>
                                </div>
                                <s class="original-sale">
                                    ￥{{ item.originalPrice }}
                                </s>
                            </div>
                        </div>
                    </div>
                </div>
            </van-list>
        </tab>
    </tabs>
</template>

<script>
import { getElementsByClassName } from '@/utils/dom';
import { searchList } from '@/api/pddMall';
import Tabs from '@/components/Tabs';
import Tab from '@/components/Tab';
import { computePrice, tabInfo } from '../config';

const page = 'PDD商品列表页';
const screen = '第一屏';

export default {
    name: 'PDDMallTabs',
    components: {
        Tabs,
        Tab,
    },
    created() {
        this.getOptList();
    },
    mounted() {
        this.computedFixedHeight();
    },
    data() {
        return {
            tabs: [],
            goodlist: [],
            selectedOptId: '',
            fixedTop: '',
            page: 1,
            pageSize: 10,
            fixTabs: false,
            loading: false,
            finished: false,
        };
    },
    watch: {
        selectedOptId(newval, oldval) {
            if (newval !== oldval) {
                // 记录一下  切换tab之前的滚动位置
                const prevTabScrollTop = this.getScrollTop();
                this.page = 1;
                // eslint-disable-next-line array-callback-return
                this.goodlist = this.goodlist.map(() => ({
                    goodsId: '',
                    name: '',
                    imgUrl: '',
                    discount: 0,
                    salesTip: 0,
                    originalPrice: 0,
                }));
                if (prevTabScrollTop > this.fixedTop) {
                    this.setScrollTop(this.fixedTop);
                }
                this.getSearchList();
            }
        },
    },
    methods: {
        getOptList() {
            this.tabs = tabInfo;
            this.selectedOptId = this.tabs[0].optId;
            this.getSearchList();
        },
        computedFixedHeight() {
            const channelTitle = getElementsByClassName('channel-title')[0];
            const channelGeneralize = getElementsByClassName('channel-generalize')[0];
            const channelTitleMargin = parseFloat(window.getComputedStyle(channelTitle).getPropertyValue('margin-bottom'));
            const channelGeneralizeMargin = parseFloat(window.getComputedStyle(channelGeneralize).getPropertyValue('margin-bottom'));
            // eslint-disable-next-line max-len
            this.fixedTop = channelTitle.clientHeight + channelGeneralize.clientHeight + channelTitleMargin + channelGeneralizeMargin;
        },
        getSearchList() {
            this.loading = true;
            searchList({
                optId: this.selectedOptId,
                page: this.page,
                pageSize: this.pageSize,
            }).then((data) => {
                this.loading = false;
                if (!data.length) {
                    this.finished = true;
                    this.$emit('finish', this.page > 1);
                }
                data.forEach((item) => {
                    // eslint-disable-next-line no-param-reassign
                    item.computePrice = this.computePrice(item.originalPrice, item.discount);
                });
                if (this.page === 1) {
                    this.goodlist = Object.freeze(data);
                } else {
                    this.goodlist = Object.freeze([...this.goodlist, ...data]);
                }
                this.page += 1;
            }).catch(() => {
                this.$error();
            });
        },
        setScrollTop(top) {
            document.documentElement.scrollTop = top;
            document.body.scrollTop = top;
        },
        getScrollTop() {
            return document.body.scrollTop || document.documentElement.scrollTop;
        },
        selectTab(data) {
            const { index } = data;
            this.$o2o({
                e_c: '分类点击',
                e_a: '点击',
                e_n: page,
                e_x: {
                    name: this.tabs[index].optName,
                },
            });
            this.selectedOptId = this.tabs[index].optId;
        },
        goGoodsDetail(goodsId) {
            this.$o2o({
                e_c: '商品列表',
                e_a: '点击',
                e_n: page,
                e_x: {
                    path: screen,
                },
            });
            this.$router.push({
                name: 'pddMallGoodsDetail',
                query: {
                    goodsId,
                },
            });
        },
        computePrice(originalPrice, discount) {
            return computePrice(originalPrice, discount);
        },
    },

};
</script>

